<template>
  <div>
    <!-- 表格组件 -->
    <h1 style="text-align: center">课程信息</h1>
    <el-table :data="dataList" border="">
      <el-table-column label="时间" width="120" prop="name"></el-table-column>
      <el-table-column label="周一" prop="time1"></el-table-column>
      <el-table-column label="周二" prop="time2"></el-table-column>
      <el-table-column label="周三" prop="time3"></el-table-column>
      <el-table-column label="周四" prop="time4"></el-table-column>
      <el-table-column label="周五" prop="time5"></el-table-column>
      <el-table-column label="周六" prop="time6"></el-table-column>
      <el-table-column label="周日" prop="time7"></el-table-column>
    </el-table>
    <br />
    <br />
    <el-button type="primary" @click="removeToken">退出登录</el-button>
    <br>
    <div v-if="this.$store.state.userToken === 'admin'">
      <hr>
      <p>管理员功能：</p>
      <el-button type="success" @click="showDialog = true">添加课程</el-button>
    <el-button type="primary" @click="userManage">用户管理</el-button>
    </div>

    <!-- 弹层组件 -->
    <el-dialog :visible="showDialog" title="添加课程" @close="btnCancel">
      <el-form label-width="100px" ref="courseForm" :model="courseOne" :rules="rules">
        <el-form-item label="课程名：" prop="course_name">
          <el-input v-model="courseOne.course_name"></el-input>
        </el-form-item>

        <el-form-item label="授课教师：" prop="teacher_name">
          <el-input v-model="courseOne.teacher_name"></el-input>
        </el-form-item>

        <el-form-item label="授课班级：" prop="class_id">
          <el-select v-model="courseOne.class_id">
            <el-option label="19软工1班" value="19软工1班"></el-option>
            <el-option label="19软工2班" value="19软工2班"></el-option>
            <el-option label="19软工3班" value="19软工3班"></el-option>
            <el-option label="19软工4班" value="19软工4班"></el-option>
            <el-option label="19软工5班" value="19软工5班"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="上课教室：" prop="room_name">
          <el-select v-model="courseOne.room_name">
            <el-option label="c101" value="c101"></el-option>
            <el-option label="c102" value="c102"></el-option>
            <el-option label="c103" value="c103"></el-option>
            <el-option label="c201" value="c201"></el-option>
            <el-option label="c202" value="c202"></el-option>
            <el-option label="c203" value="c203"></el-option>
          </el-select>
        </el-form-item>

        <div>
          <span>&nbsp;&nbsp;&nbsp;上课时间:&nbsp;&nbsp;</span>
          <el-select v-model="courseTime.time1">
            <el-option label="周一" value="1"></el-option>
            <el-option label="周二" value="2"></el-option>
            <el-option label="周三" value="3"></el-option>
            <el-option label="周四" value="4"></el-option>
            <el-option label="周五" value="5"></el-option>
            <el-option label="周六" value="6"></el-option>
            <el-option label="周日" value="7"></el-option>
          </el-select>
          <el-select v-model="courseTime.time2">
            <el-option label="第一节" value="_1"></el-option>
            <el-option label="第二节" value="_2"></el-option>
            <el-option label="第三节" value="_3"></el-option>
            <el-option label="第四节" value="_4"></el-option>
            <el-option label="第五节" value="_5"></el-option>
            <el-option label="第六节" value="_6"></el-option>
            <el-option label="第七节" value="_7"></el-option>
            <el-option label="第八节" value="_8"></el-option>
          </el-select>
        </div>
        <br>
        <br>

        <el-form-item label="上课时间：" style="display: none">
          <el-input v-model="courseOne.time"></el-input>
        </el-form-item>
        
        <el-row type="flex" justify="center">
          <el-button type="primary" @click="btnSubmit">确定</el-button>
          <el-button @click="btnCancel">取消</el-button>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { changeForCourse } from "@/utils/DataChange";
import { getSyInfoList, addSyInfo } from "@/api/syinfo";

export default {
  data() {
    return {
      // 弹层控制
      showDialog: false,
      // 课程时间对象
      courseTime: {
        time1: '1',
        time2: '_1'
      },
      // 添加课程对象
      courseOne: {
        course_name: "",
        teacher_name: "",
        class_id: "19软工1班",
        room_name: "c101",
        time: '',
      },
      // 表格校验
      rules: {
        course_name: [
          { required: 'true', trigger: 'blur', message: '课程名都不填' }
        ],
        teacher_name: [
          { required: 'true', trigger: 'blur', message: '授课老师没填' }
        ],
        class_id: [
          { required: 'true', trigger: 'blur', message: '教哪个班？' }
        ],
        room_name: [
          { required: 'true', trigger: 'blur', message: '哪个教室来着？' }
        ]
      },
      // 表格数据
      dataList: [],
      courseList: [],
      // 测试数据
      // courseList: [
      //   {
      //     time: "1_2",
      //     class_id: "2019级软件工程3班",
      //     room_name: "毓秀二机房",
      //     teacher_name: "成华",
      //     course_name: "c语言程序设计",
      //   },
      //   {
      //     time: "5_2",
      //     class_id: "2019级软件工程1班",
      //     room_name: "d304",
      //     teacher_name: "王刚",
      //     course_name: "毛概",
      //   },
      // ],
    };
  },
  methods: {
    async changeForCourse() {
      // 调用接口获取数据
      const { data } = await getSyInfoList();
      // 转化数据
      this.dataList = changeForCourse(data.data);
      console.log('课表信息： ' , data.data)

      // // 临时
      // this.dataList = changeForCourse(this.courseList);
    },

    removeToken() {
      // 删除token
      this.$store.commit("removeToken");
      this.$router.go("/login");
    },

    // 添加课程
    btnSubmit() {
      // 手动校验表单
      this.$refs.courseForm.validate(async isOK => {
        if (isOK) {
          // 表单校验通过
          // 拼接上课时间
          this.courseOne.time = this.courseTime.time1 + this.courseTime.time2

          // 调用接口添加
          const { data } = await addSyInfo(this.courseOne)
          console.log(data)

          if (data.data === 1) {
            // 添加成功
            // 提示信息
            this.$message.success('添加成功！')
            // 更新表格
            this.changeForCourse()
            // 关闭弹层
            this.showDialog = false

          }else {
            // 添加失败
            this.$message.error('添加失败，该时段已有课了！')
          }
        }
      })
    },

    // 关闭弹层
    btnCancel() {
      // 重置数据
      this.courseOne = {
        course_name: "",
        teacher_name: "",
        class_id: "19软工1班",
        room_name: "c101",
        time: '',
      },
      // 关闭弹层
      this.showDialog = false
      // 清空校验
      this.$refs.courseForm.resetFields()
    },

    // 用户管理
    userManage() {
      this.$router.push('/user')
    }
  },
  created() {
    this.changeForCourse();
  }
};
</script>

