import Vue from 'vue'
import Vuex from 'vuex'
import Cookie from 'js-cookie'

// 注册Vuex
Vue.use(Vuex)

// 用户信息在Cookie中的key
const userToken = 'acss-token'

const state = {

    userToken: Cookie.get(userToken), // 登录信息

    // 用户信息
    userInfo: {} // 加载vuex的时候从缓存中读取用户信息
}

const mutations = {

    // 登录、设置token
    setUserToken(state, data) {
        state.userToken = data,
        Cookie.set(userToken, data)
    },

    // 删除token
    removeToken() {
      Cookie.remove(userToken)  
    },

    // 设置用户信息
    setUserInfo(state, data) {
        console.log(data.name)
        state.userInfo = data
    }
}

const actions = {

}

const store = new Vuex.Store({
    state,
    mutations,
    actions
})

export default store
