<template>
  <div>
    <el-dialog :visible="showDialog" width="600px" @close="btnCancel">
      <h1 style="text-align: center; margin-top: -20px">用户注册</h1>
      <el-form
        :model="formData"
        status-icon
        ref="userForm"
        label-width="100px"
        :rules="rules"
      >
        <el-form-item label="用户名：" prop="username">
          <el-input
            type="text"
            v-model="formData.username"
            placeholder="请输入用户名"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码：" prop="password">
          <el-input
            type="password"
            v-model="formData.password"
            autocomplete="off"
            placeholder="请输入密码"
          ></el-input>
        </el-form-item>
        <el-form-item label="确认密码：" prop="confirmPwd">
          <el-input
            type="password"
            v-model="formData.confirmPwd"
            autocomplete="off"
            placeholder="请确认密码"
          ></el-input>
        </el-form-item>
        <el-form-item label="邮箱：" prop="email">
          <el-input
            type="text"
            v-model="formData.email"
            placeholder="请输入邮箱"
            @keyup.enter.native="btnSubmit"
          ></el-input>
        </el-form-item>
        <el-row type="flex" justify="center">
          <el-button @click="btnSubmit" type="primary">注册</el-button>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { userRegister } from "@/api/user";

export default {
  props: {
    showDialog: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    const validPwd = (rule, value, callback) => {
      if (value !== this.formData.password) {
        callback(new Error("两次密码不一样"));
      } else {
        callback();
      }
    };

    return {
      // 用户提交的信息
      formData: {
        username: "",
        password: "",
        confirmPwd: "",
        email: "",
        identity: "user",
      },
      rules: {
        username: [
          { required: true, trigger: "blur", message: "拜托输入用户名" },
          { min: 3, max: 16, trigger: "blur", message: "长度在3-16位之间" },
        ],
        password: [
          { required: true, trigger: "blur", message: "拜托输入密码" },
          { min: 6, max: 16, trigger: "blur", message: "长度在6-16位之间" },
        ],
        confirmPwd: [{ validator: validPwd, trigger: "blur" }],
        email: [
          { required: true, trigger: "blur", message: "拜托输入邮箱" },
          { min: 0, max: 30, trigger: "blur", message: "你邮箱这么长？" },
          { type: "email", trigger: "blur", message: "这是邮箱？" },
        ],
      },
    };
  },

  methods: {
    btnSubmit() {
      // 手动进行表单校验
      this.$refs.userForm.validate(async (isOK) => {
        if (isOK) {
          // 校验通过
          // 调用接口-注册
          const { data } = await userRegister(this.formData);
          console.log(data);

          if (data.code == 100) {
            // 注册成功
            // 设置信息
            this.$store.commit("setUserToken", this.formData.identity);
            // 提示信息，跳转页面
            this.$message.success("注册成功！");
            this.$router.go('/')
          }else {
            
            // 注册失败
            this.$message.error(data.data)
          }
        }
      });
    },

    btnCancel() {
      // 重置数据
      (this.formData = {
        username: "",
        password: "",
        confirmPwd: "",
        email: "",
        identity: "user",
      }),
        // 关闭弹层
        this.$emit("update:showDialog", false);
      // 清空校验
      this.$refs.userForm.resetFields();
    },
  },
};
</script>

