import request from '@/utils/request'

// 用户登录
export function userLogin(params) {
    return request({
        url: 'http://localhost:8080/user-servlet',
        method: 'post',
        params: {
            func: 'getUserByNameAndPwd',
            ...params
        }
    })
}

// 用户注册
export function userRegister(params) {
    return request({
        url: 'http://localhost:8080/user-servlet',
        method: 'post',
        params: {
            func: 'addUser',
            ...params
        }
    })
}

// 获取所有用户列表
export function getUserList() {
    return request({
        url: 'http://localhost:8080/user-servlet?func=getUserList',
    })
}

// 根据id获取用户信息
export function getUserById(user_id) {
    return request({
        url: 'http://localhost:8080/user-servlet',
        params: {
            func: 'findUserById',
            user_id
        }
    })
}

// 修改用户信息
export function updateUser(data) {
    return request({
        url: 'http://localhost:8080/user-servlet?func=updateUser',
        method: 'post',
        data
    })
}

// 删除用户
export function deleteUser(user_id) {
    return request({
        url: 'http://localhost:8080/user-servlet',
        method: 'post',
        params: {
            func: 'deleteUser',
            user_id
        }
    })
}
