<template>
  <div id="box_body">
    <div class="login_div">
      <el-card id="box_div">
        <h1 style="text-align: center">用户登录</h1>
        <el-form
          :model="formData"
          status-icon
          ref="userForm"
          label-width="100px"
          :rules="rules"
        >
          <el-form-item label="用户名：" prop="username">
            <el-input
              type="text"
              v-model="formData.username"
              placeholder="请输入用户名"
            ></el-input>
          </el-form-item>
          <el-form-item label="密码：" prop="password">
            <el-input
              type="password"
              v-model="formData.password"
              autocomplete="off"
              placeholder="请输入密码"
              @keyup.enter.native="btnSubmit"
            ></el-input>
          </el-form-item>

          <el-row type="flex" justify="center">
            <el-button @click="btnSubmit" type="primary">登录</el-button>
            <el-button @click="showDialog = true">注册</el-button>
          </el-row>
        </el-form>
      </el-card>
    </div>
    <user-register :showDialog.sync="showDialog"></user-register>
  </div>
</template>

<script>
import UserRegister from "@/components/userRegister.vue";
import { userLogin } from "@/api/user";

export default {
  components: {
    UserRegister,
  },

  data() {
    return {
      // 注册弹层
      showDialog: false,
      // 用户提交的信息
      formData: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          { required: true, trigger: "blur", message: "拜托输入用户名" },
          { min: 3, max: 16, trigger: "blur", message: "长度在3-16位之间" },
        ],
        password: [
          { required: true, trigger: "blur", message: "拜托输入密码" },
          { min: 6, max: 16, trigger: "blur", message: "长度在6-16位之间" },
        ],
      },
    };
  },

  methods: {
    btnSubmit() {
      // 手动进行表单校验
      this.$refs.userForm.validate(async (isOK) => {
        if (isOK) {
          // 校验通过
          // 调用接口，判断是否登录成功
          const { data } = await userLogin(this.formData);
          if (data.code === -1) {
            this.$message.error(data.data);
          } else {
            this.formData = data.data;
            // 登录成功、设置信息
            this.$store.commit("setUserToken", this.formData.identity);
            this.$store.commit("setUserInfo", this.formData);
            console.log(this.$store.state.userInfo.identity);

            // 提示信息、跳转页面
            this.$message.success("登录成功！");
            this.$router.go("/");
          }
        }
      });
    },
  },
};
</script>

<style scoped>
.login_div {
  margin-top: 13%;
  margin-left: 35%;
}

#box_div {
  width: 500px;
  background: #c2e8fa;
  border-radius: 20px;
}
</style>