/**
 * // 总路由配置
 */
import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store'
import notFind from '@/components/notFind'
import userLogin from '@/components/userLogin'
import showData from '@/components/showData'
import showUser from '@/components/showUser'
Vue.use(Router)

const routes = [
    // 首页
    {
        path: '/',
        redirect: '/show-data'
    },

    // 展示页
    {
        path: '/show-data',
        component: showData
    },

    // 登录页
    {
        path: '/login',
        component: userLogin
    },

    // 用户信息页面
    {
        path: '/user',
        component: showUser
    },

    // 404
    {
        path: '/404',
        component: notFind
    },

    // 其他所有页面 
    {
        path: '*',
        component: notFind
    }
]

const createRouter = new Router({
    // mode: 'history',
    // base: '/acss',
    routes
})

const router = createRouter // 实例化路由

// 前置守卫
router.beforeEach((to, form, next) => {
    if (store.state.userToken) {
        // 缓存中有userInfo，代表已登录
        if (to.path === '/login') {
            // 如果访问登录页，则跳转主页
            next('/')
        }else {
            // 放行
            next()
        }
    }else {
        // 缓存中没有userInfo
        // 跳转登录页
        if (to.path === '/login') {
            next()
        }else {
            next('/login')
        }
    }
})

export default router