/**
 * 数据结构转换工具
 */

/**
 * 课表对象转化为展示结构
 * @param {Object} data 
 */
export function changeForCourse(data) {

  let dataList = [
    // 第一节
    {
      name: "第一节",
      time1: "",
      time2: "",
      time3: "",
      time4: "",
      time5: "",
      time6: "",
      time7: "",
    },
    // 第二节
    {
      name: "第二节",
      time1: "",
      time2: "",
      time3: "",
      time4: "",
      time5: "",
      time6: "",
      time7: "",
    },
    // 第三节
    {
      name: '第三节',
      time1: "",
      time2: "",
      time3: "",
      time4: "",
      time5: "",
      time6: "",
      time7: "",
    },
    // 第四节
    {
      name: '第四节',
      time1: "",
      time2: "",
      time3: "",
      time4: "",
      time5: "",
      time6: "",
      time7: "",
    },
    // 第五节
    {
      name: '第五节',
      time1: "",
      time2: "",
      time3: "",
      time4: "",
      time5: "",
      time6: "",
      time7: "",
    },
    // 第六节
    {
      name: '第六节',
      time1: "",
      time2: "",
      time3: "",
      time4: "",
      time5: "",
      time6: "",
      time7: "",
    },
    // 第七节
    {
      name: '第七节',
      time1: "",
      time2: "",
      time3: "",
      time4: "",
      time5: "",
      time6: "",
      time7: "",
    },
    // 第八节
    {
      name: '第八节',
      time1: "",
      time2: "",
      time3: "",
      time4: "",
      time5: "",
      time6: "",
      time7: "",
    },
  ]

  data.forEach(item => {
    const temp = item.time.split('_')
    // 第几周
    const week = parseInt(temp[1]) - 1
    // 第几节课
    const lesson = 'time' + temp[0]
    
    dataList[week][lesson] = item.course_name + '( ' + item.room_name + ' )'
  });

  return dataList
}
