<template>
  <div>
    <h1 style="text-align: center">用户管理</h1>
    <el-table :data="userList" border>
      <el-table-column label="用户名" prop="username"></el-table-column>
      <el-table-column label="邮箱" prop="email"></el-table-column>
      <el-table-column
        label="用户身份"
        prop="identity"
        :formatter="formatIdentity"
      ></el-table-column>
      <el-table-column
        label="创建时间"
        prop="d_create"
        :formatter="formatDate"
      ></el-table-column>
      <el-table-column
        label="修改时间"
        prop="d_modified"
        :formatter="formatDate"
      ></el-table-column>
      <el-table-column label="操作">
        <template v-slot="{ row }">
          <el-row type="flex" justify="center">
            <el-button type="primary" @click="findUserById(row.id)"
              >修改</el-button
            >
            <el-button type="danger" @click="deleteUser(row.id)"
              >删除</el-button
            >
          </el-row>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog :visible="showDialog" title="修改用户" @close="btnCancel">
      <el-form
        label-width="100px"
        ref="updateForm"
        :model="user"
        :rules="rules"
      >
        <el-form-item label="用户名：" prop="username">
          <el-input v-model="user.username"></el-input>
        </el-form-item>
        <el-form-item label="邮箱：" prop="email">
          <el-input v-model="user.email"></el-input>
        </el-form-item>
        <el-form-item label="用户身份：" prop="identity">
          <el-select v-model="user.identity">
            <el-option label="管理员" value="admin"></el-option>
            <el-option label="普通用户" value="user"></el-option>
          </el-select>
        </el-form-item>
        <el-row type="flex" justify="center">
          <el-button type="primary" @click="btnSubmit">确定</el-button>
          <el-button @click="btnCancel">取消</el-button>
        </el-row>
      </el-form>
    </el-dialog>
    <br />
    <el-button type="primary" @click="toHome">返回课程表</el-button>
  </div>
</template>

<script>
import { getUserList, getUserById, updateUser, deleteUser } from "@/api/user";

export default {
  data() {
    return {
      // 弹层控制
      showDialog: false,

      // 用户对象
      user: {
        id: null,
        username: "",
        email: "",
        identity: "",
      },

      // 表单校验
      rules: {
        username: [
          { required: true, trigger: "blur", message: "用户名没填啊！" },
          {
            min: 3,
            max: 16,
            trigger: "blur",
            message: "用户名在3-16位之间哦！",
          },
        ],
        email: [
          { required: true, trigger: "blur", message: "邮箱没填啊！" },
          { type: "email", trigger: "blur", message: "你邮箱介个样子滴？" },
        ],
      },

      // 用户列表
      userList: [],

      // 测试数据
      dataList: [
        {
          d_create: 1651670014000,
          d_modified: 1651670014000,
          email: "okk@qq.com",
          id: 11,
          identity: "admin",
          password: "E10ADC3949BA59ABBE56E057F20F883E",
          username: "admin",
        },
        {
          d_create: 1651670032000,
          d_modified: 1651670032000,
          email: "sss@ss.com",
          id: 12,
          identity: "user",
          password: "E10ADC3949BA59ABBE56E057F20F883E",
          username: "zhangsan",
        },
        {
          d_create: 1651670040000,
          d_modified: 1651670040000,
          email: "sss@ss.com",
          id: 13,
          identity: "user",
          password: "E10ADC3949BA59ABBE56E057F20F883E",
          username: "isi",
        },
        {
          d_create: 1651727535000,
          d_modified: 1651727535000,
          email: "asd@qq.com",
          id: 14,
          identity: "user",
          password: "A8F5F167F44F4964E6C998DEE827110C",
          username: "asd",
        },
      ],
    };
  },
  methods: {
    async getUserList() {
      // 调用接口获取用户数据
      const { data } = await getUserList();
      console.log(data.data);
      this.userList = data.data;
      //   // 转化用户数据
      //   this.userList = this.dataList
    },

    // 点击修改
    async findUserById(id) {
      // 开启弹层
      this.showDialog = true;
      // 调用接口，获取用户信息
      const { data } = await getUserById(id);
      this.user = data.data;
    },

    // 删除用户
    async deleteUser(id) {
      try {
        await this.$confirm("确定删除用户？");
        // 用户点击确定后，自动执行下面代码
        // 调用接口。删除员工
        await deleteUser(id);
        // 提示信息
        this.$message.success("删除成功！");
        // 重新拉取数据
        this.getUserList();
      } catch (error) {
        console.log(error);
      }
    },

    // 更新用户
    async btnSubmit() {
      // 表单校验
      this.$refs.updateForm.validate(async (isOK) => {
        if (isOK) {
          // 校验成功
          // 调用接口，更新用户信息
          const { data } = await updateUser(this.user);

          if (data.code == 100) {
            // 修改成功
            // 提示用户
            this.$message.success("修改成功！");
            // 关闭弹层
            this.showDialog = false;
            // 重新获取数据
            this.getUserList();
          } else {
            // 修改失败，提示信息
            this.$message.error(data.data);
          }
        }
      });
    },

    // 关闭弹层
    btnCancel() {
      // 重置数据
      this.user = {
        id: null,
        username: "",
        email: "",
      };
      // 关闭弹层
      this.showDialog = false;
      // 清空校验
      this.$refs.updateForm.resetFields();
    },

    // 返回课程列表
    toHome() {
      this.$router.push("/");
    },

    // 格式化用户身份
    formatIdentity(row, col, cellValue) {
      return cellValue === "admin" ? "管理员" : "普通用户";
    },

    // 格式化时间
    formatDate(row, col, date) {
      let fmt = "yyyy-MM-dd hh:mm:ss";
      if (!(date instanceof Array)) {
        date = new Date(date);
      }
      if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          (date.getFullYear() + "").substr(4 - RegExp.$1.length)
        );
      }
      const o = {
        "M+": date.getMonth() + 1,
        "d+": date.getDate(),
        "h+": date.getHours(),
        "m+": date.getMinutes(),
        "s+": date.getSeconds(),
      };
      for (const k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
          const str = o[k] + "";
          fmt = fmt.replace(
            RegExp.$1,
            RegExp.$1.length === 1 ? str : this.padLeftZero(str)
          );
        }
      }
      return fmt;
    },

    // 时间+00
    padLeftZero(str) {
      return ("00" + str).substr(str.length);
    },
  },
  created() {
    this.getUserList();
  },
  filters: {
    filterIdentity(value) {
      if (value === "admin") {
        return "管理员";
      } else {
        return "普通用户";
      }
    },
  },
};
</script>

