import request from '@/utils/request'

export function getSyInfoList() {
    return request({
        url: 'http://localhost:8080/sys?func=findAll',
        method: 'post'
    })
}

export function addSyInfo(data) {
    return request({
        url: 'http://localhost:8080/sys?func=addSyInfo',
        method: 'post',
        data
    })
}
